import React from "react";
import { DefaultSelect } from "templates/input";

export default function CommissionsTable() {
  return (
    <div className="w-100 d-flex flex-column px-5">
      <DefaultSelect
        label="Proveedor"
        customClass="col-12"
        placeholder="Selecciona una opción"
        options={[
          { value: "1", name: "Opción 1" },
          { value: "2", name: "Opción 2" },
        ]}
      />

      <Headers />

      <div className="w-100 d-flex flex-column border">
        <TableRow check />
        <TableRow />
      </div>
    </div>
  );
}

const TableRow = ({ check }) => {
  return (
    <div className="row w-100 mx-0 align-items-center border">
      <span className="col-1 text-center border-end py-2">1</span>
      <span className="col-3 text-center border-end py-2">Claro</span>
      <span className="col-3 text-center border-end py-2">Recarga [1]</span>
      <span className="col-3 text-center border-end py-2">5.00%</span>
      <div className="col-2 d-flex align-items-center justify-content-center py-2">
        {!check ? (
          <button className="btn btn-outline-secondary btn-sm">
            <i className="fas fa-check"></i>
          </button>
        ) : (
          <button className="btn btn-success btn-sm">
            <i className="fas fa-check"></i>
          </button>
        )}
      </div>
    </div>
  );
};

const Headers = () => {
  return (
    <div className="row w-100 mx-0 bg-secondary text-white rounded mb-2">
      <span className="col-1 text-center border-end py-2">ID</span>
      <span className="col-3 text-center border-end py-2">Proveedor</span>
      <span className="col-3 text-center border-end py-2">Producto</span>
      <span className="col-3 text-center border-end py-2">Comisión</span>
      <span className="col-2 text-center py-2">Opciones</span>
    </div>
  );
};
