import React, { useState } from "react";

import ContentContainer from "components/layout/ContentContainer";
import OperationsCreditList from "components/Operation/Credit/OperationsCreditsList";
import CreditForm from "components/Operation/Credit/CreditForm";

export default function Credit() {
  const [credit, setCredit] = useState(null);

  const closeFunction = () => {
    setCredit(null);
  };

  return (
    <ContentContainer>
      <h2 className="py-5">Crédito</h2>

      <OperationsCreditList setCredit={setCredit} />

      {credit && <CreditForm closeFunction={closeFunction} />}
    </ContentContainer>
  );
}
