import React from "react";

export default function ListHeaders() {
  return (
    <div className="row w-100 mx-0 bg-secondary text-white rounded mb-2">
      <span className="col-1 text-center border-end py-2">ID</span>
      <span className="col-3 text-center border-end py-2">Código</span>
      <span className="col-3 text-center border-end py-2">Identificación</span>
      <span className="col-4 text-center border-end py-2">Nombre</span>
      <span className="col-1 text-center py-2">Información</span>
    </div>
  );
}
