import React from "react";

import ListSearch from "components/Operation/Debit/ListSearch";
import ListHeaders from "components/Operation/Debit/ListHeaders";
import DebitListItem from "components/Operation/Debit/DebitListItem";

export default function OperationDebitsList({ setDebit }) {
  return (
    <div className="w-100 border p-3 d-flex flex-column mb-4">
      <ListSearch />

      <ListHeaders />

      <DebitListItem setDebit={setDebit} />
    </div>
  );
}
