import React from "react";

import { DefaultCheckbox, DefaultInput, DefaultSelect } from "templates/input";
import CommissionsTable from "./CommissionsTable";

export default function BusinessForm({
  noBank,
  title,
  closeBtn,
  closeFunction,
}) {
  return (
    <form className="w-100 d-flex flex-column" autoComplete="off">
      <div className="d-flex justify-content-between align-items-center">
        {title && <h2 className="text-center mb-4">Detalles de comercio</h2>}
        {closeBtn && (
          <button
            onClick={closeFunction}
            type="button"
            className="btn btn-danger"
          >
            <i className="fas fa-times me-2"></i>
            Cerrar
          </button>
        )}
      </div>

      <div className="p-3 border row mx-0 rounded mb-5">
        <h5 className="text-start mb-3">Información General</h5>

        {/* row 1 */}
        <DefaultInput label="Padre" customClass="col-12 col-md-6 col-lg-3" />
        <DefaultInput label="Tipo" customClass="col-12 col-md-6 col-lg-3" />
        <DefaultInput label="Código" customClass="col-12 col-md-6 col-lg-3" />
        <DefaultInput label="Nombre" customClass="col-12 col-md-6 col-lg-3" />

        <DefaultSelect
          label="País"
          customClass="col-12 col-md-6 col-lg-3"
          placeholder="Selecciona una opción"
          options={[
            { value: "1", name: "Opción 1" },
            { value: "2", name: "Opción 2" },
          ]}
        />
        <DefaultSelect
          label="Provincia"
          customClass="col-12 col-md-6 col-lg-3"
          placeholder="Selecciona una opción"
          options={[
            { value: "1", name: "Opción 1" },
            { value: "2", name: "Opción 2" },
          ]}
        />
        <DefaultSelect
          label="Cantón"
          customClass="col-12 col-md-6 col-lg-3"
          placeholder="Selecciona una opción"
          options={[
            { value: "1", name: "Opción 1" },
            { value: "2", name: "Opción 2" },
          ]}
        />
        <DefaultSelect
          label="Parroquia"
          customClass="col-12 col-md-6 col-lg-3"
          placeholder="Selecciona una opción"
          options={[
            { value: "1", name: "Opción 1" },
            { value: "2", name: "Opción 2" },
          ]}
        />

        <DefaultSelect
          label="Tipo de comercio"
          customClass="col-12 col-md-6"
          placeholder="Selecciona una opción"
          options={[
            { value: "1", name: "Opción 1" },
            { value: "2", name: "Opción 2" },
          ]}
        />
        <DefaultSelect
          label="Vendedor"
          customClass="col-12 col-md-6"
          placeholder="Selecciona una opción"
          options={[
            { value: "1", name: "Opción 1" },
            { value: "2", name: "Opción 2" },
          ]}
        />

        <DefaultCheckbox
          label="Activar"
          customClass="col-3 offset-4 d-flex align-items-center justify-content-center gap-2 fw-bold mt-4"
        />
      </div>

      {!noBank && (
        <div className="p-3 border row mx-0 rounded mb-5">
          <h5 className="text-start mb-3">Sección banco central</h5>

          <DefaultInput
            label="Usuario para Banco Central"
            customClass="col-12"
          />
          <DefaultInput label="Pin de Usuario" customClass="col-12" />
          <DefaultCheckbox
            label="¿Acreditar saldos de transacciones en billetera electrónica?"
            customClass="col-3 offset-4 d-flex align-items-center justify-content-center gap-2 fw-bold mt-4"
          />
        </div>
      )}

      <div className="p-3 border row mx-0 rounded mb-5">
        <h5 className="text-start mb-3">Información de facturación</h5>

        <DefaultSelect
          label="Tipo de identificación"
          customClass="col-12"
          placeholder="Selecciona una opción"
          options={[
            { value: "1", name: "Opción 1" },
            { value: "2", name: "Opción 2" },
          ]}
        />
        <DefaultInput label="Identifiación" customClass="col-12" />
        <DefaultInput label="Dirección" customClass="col-12" />
      </div>

      <div className="p-3 border row mx-0 rounded mb-5">
        <h5 className="text-start mb-3">Información del contacto</h5>

        <DefaultInput customClass="col-12 col-md-6" label="Nombre" />
        <DefaultInput customClass="col-12 col-md-6" label="Apellido" />
        <DefaultInput customClass="col-12 col-md-6" label="Correo" />
        <DefaultInput customClass="col-12 col-md-6" label="Teléfono" />
        <DefaultInput customClass="col-12 col-md-6" label="Dirección" />
        <DefaultCheckbox
          label="Notificación por SMS"
          customClass="col-12 col-md-3 d-flex align-items-center justify-content-center gap-2 fw-bold"
        />
        <DefaultCheckbox
          label="Notificación por correo"
          customClass="col-12 col-md-3 d-flex align-items-center justify-content-center gap-2 fw-bold"
        />
      </div>

      <div className="p-3 border row mx-0 rounded mb-5">
        <h5 className="text-start mb-3">Comisiones</h5>

        <CommissionsTable />
      </div>

      <button type="button" className="btn btn-primary">
        Guardar
      </button>
    </form>
  );
}
