import React from "react";

import { DefaultInput, DefaultSelect } from "templates/input";

export default function ListSearch() {
  return (
    <form
      className="w-100 d-flex align-items-end gap-3 mb-4"
      autoComplete="off"
    >
      <DefaultInput
        mb="0"
        customClass="flex-grow-1"
        label="Fecha Inicio"
        type="date"
      />

      <DefaultInput
        mb="0"
        customClass="flex-grow-1"
        label="Fecha Fin"
        type="date"
      />

      <DefaultSelect
        mb="0"
        customClass="flex-grow-1 mb-0"
        label="Banco"
        placeholder="Seleccione una opción"
        options={[
          { value: "1", name: "Opcion 1" },
          { value: "2", name: "Opcion 2" },
        ]}
      />

      <DefaultSelect
        mb="0"
        customClass="flex-grow-1"
        label="Estado"
        placeholder="Seleccione una opción"
        options={[
          { value: "1", name: "Opcion 1" },
          { value: "2", name: "Opcion 2" },
        ]}
      />

      <button type="button" className="btn btn-primary">
        <i className="fas fa-search me-2"></i>
        Buscar
      </button>
    </form>
  );
}
