import {
  HashRouter as Router,
  Redirect,
  Route,
  Switch,
} from "react-router-dom";

import { AuthProvider } from "../auth/authContext";

// import PrivateRoute from "./PrivateRoute";
// import PublicRoute from "./PublicRoute";

import Navbar from "components/layout/Navbar";
import Sidebar from "components/layout/Sidebar";

// pages
import Login from "pages/Login";

import UpdateUser from "pages/User/UpdateUser";
import UpdatePassword from "pages/User/UpdatePassword";

import BusinessInfo from "pages/Business/BusinessInfo";
import AdminMyBusiness from "pages/Business/AdminMyBusiness";
import CreateUser from "pages/Business/CreateUser";
import AdminUsers from "pages/Business/AdminUsers";

import Products from "pages/Config/Products";
import Banks from "pages/Config/Banks";

import NewBusiness from "pages/AdminBusiness/NewBusiness";
import AdminBusiness from "pages/AdminBusiness/AdminBusiness";
import AdminMyBusinessListPage from "pages/AdminBusiness/AdminMyBusinessListPage";
import AdminSellers from "pages/AdminBusiness/AdminSellers";

import Credit from "pages/Operation/Credit";
import Debit from "pages/Operation/Debit";
import AccreditationRequest from "pages/Operation/AccreditationRequest";
import AproveRequests from "pages/Operation/AproveRequests";
import ListAproveRequest from "pages/Operation/ListAproveRequest";
import ListActivationRequest from "pages/Operation/ListActivationRequest";

//prettier-ignore
export default function AppRouter() {
  return (
    <Router>
      <AuthProvider>
      <div className="app-content">
        <Sidebar />

        <div className="body-content">
          <Navbar />

          <Switch>
            <Route exact path="/" component={Login}/>

            <Route exact path="/userUpdate" component={UpdateUser}/>
            <Route exact path="/userPass" component={UpdatePassword}/>

            <Route exact path="/businessInfo" component={BusinessInfo}/>
            <Route exact path="/businessAdmin" component={AdminMyBusiness}/>
            <Route exact path="/businessNewAdmin" component={CreateUser}/>
            <Route exact path="/businessUsers" component={AdminUsers}/>
            <Route exact path="/businessUsers/:id" component={AdminUsers}/>

            <Route exact path="/configProducts" component={Products}/>
            <Route exact path="/configBanks" component={Banks}/>

            <Route exact path="/adminNew" component={NewBusiness}/>
            <Route exact path="/adminBusiness" component={AdminBusiness}/>
            <Route exact path="/adminMyBusiness" component={AdminMyBusinessListPage}/>
            <Route exact path="/adminSellers" component={AdminSellers}/>

            <Route exact path="/opsCredit" component={Credit}/>
            <Route exact path="/opsDebit" component={Debit}/>
            <Route exact path="/opsRequest" component={AccreditationRequest}/>
            <Route exact path="/opsAprove" component={AproveRequests}/>
            <Route exact path="/opsListAprove" component={ListAproveRequest}/>
            <Route exact path="/opsListActivation" component={ListActivationRequest}/>

            <Route path="*">
              <Redirect to="/userUpdate" />
            </Route>
          </Switch>
        </div>
      </div>
      </AuthProvider>
    </Router>
  );
}
