import React, { useState } from "react";

import ContentContainer from "components/layout/ContentContainer";
import AdminMyBusinessList from "components/AdminBusiness/AdminMyBusiness/AdminMyBusinessList";
import TabsContainer from "components/Business/BusinessInfo/TabsContainer";

export default function AdminMyBusinessListPage() {
  const [business, setBusiness] = useState(null);
  const [tab, setTab] = useState(1);

  const handleBusiness = (id) => {
    setBusiness(id);
  };

  const handleTab = ({ target }) => {
    setTab(Number(target.value));
  };

  const closeBusiness = () => {
    setBusiness(null);
  };

  return (
    <ContentContainer>
      <h2 className="py-5">Administrar mis comercios</h2>

      <AdminMyBusinessList setBusiness={handleBusiness} />

      {business && (
        <TabsContainer
          title="Información de comercio"
          closeBtn
          closeFunction={closeBusiness}
          tab={tab}
          handleTab={handleTab}
          hierarchy
        />
      )}
    </ContentContainer>
  );
}
