import React from "react";

export default function DatePicker() {
  return (
    <div className="w-100 row mx-0 align-items-end border py-3 mb-4">
      <div className="col-12 col-md-3 offset-md-2">
        <label htmlFor="dateStart" className="form-label">
          Fecha Inicio
        </label>
        <input type="date" className="form-control" id="dateStart" />
      </div>

      <div className="col-12 col-md-3">
        <label htmlFor="dateEnd" className="form-label">
          Fecha Final
        </label>
        <input type="date" className="form-control" id="dateEnd" />
      </div>

      <button type="button" className="btn btn-success col-12 col-md-2">
        Buscar
      </button>
    </div>
  );
}
