import React, { useState } from "react";

import ContentContainer from "components/layout/ContentContainer";
import AdminBusinessList from "components/AdminBusiness/AdminBusiness/AdminBusinessList";
import BusinessForm from "components/AdminBusiness/NewBusiness/BusinessForm";

export default function AdminBusiness() {
  const [business, setBusiness] = useState(null);

  const closeBusiness = () => {
    setBusiness(null);
  };

  return (
    <ContentContainer>
      <h2 className="py-5">Administrar comercio</h2>

      <AdminBusinessList setBusiness={setBusiness} />

      {business && (
        <BusinessForm noBank title closeBtn closeFunction={closeBusiness} />
      )}
    </ContentContainer>
  );
}
