import React from "react";

import ListSearch from "components/Operation/Credit/ListSearch";
import ListHeaders from "components/Operation/Credit/ListHeaders";
import CreditListItem from "components/Operation/Credit/CreditListItem";

export default function OperationsCreditList({ setCredit }) {
  return (
    <div className="w-100 border p-3 d-flex flex-column mb-4">
      <ListSearch />

      <ListHeaders />

      <CreditListItem setCredit={setCredit} />
    </div>
  );
}
