import React, { useState } from "react";

import ContentContainer from "components/layout/ContentContainer";
import AccreditationForm from "components/Operation/AccreditationRequest/AccreditationForm";
import AccreditationList from "components/Operation/AccreditationRequest/AccreditationList";

export default function AccreditationRequest() {
  const [list, setList] = useState(false);

  const toggleList = () => {
    setList(!list);
  };

  return (
    <ContentContainer>
      <h2 className="py-5">Solicitud de acreditación</h2>

      <div className="w-100 d-flex justify-content-start align-items-center mb-4">
        <button onClick={toggleList} type="button" className="btn btn-primary">
          {!list ? "Ver solicitudes" : "Formulario"}
        </button>
      </div>

      {!list && <AccreditationForm />}

      {list && <AccreditationList />}
    </ContentContainer>
  );
}
