import React from "react";

import "styles/BusinessStyles.css";

export default function UsersList({ title }) {
  return (
    <div className="w-25 d-flex flex-column border p-2 rounded">
      <h5 className="text-start mb-3">{title}</h5>

      <div className="w-100 d-flex flex-column border p-1">
        <UserRow name="Usuario 1" />
        <UserRow name="Usuario 2" />
        <UserRow name="Usuario 3" />
      </div>
    </div>
  );
}

const UserRow = ({ name }) => {
  return (
    <div className="business-user-list-item w-100 d-flex align-items-center py-1 mb-2 rounded">
      <i className="fas fa-check text-success mx-2"></i>
      <p className="mb-0 word-break me-2">{name}</p>
    </div>
  );
};
