import React from "react";

export default function AccreditationsReceived() {
  return (
    <section className="w-100 d-flex flex-column">
      <Headers />

      <DataRow />
      <DataRow />
      <DataRow />
      <DataRow />
      <DataRow />
      <DataRow />
    </section>
  );
}

const Headers = () => {
  //prettier-ignore
  return (
    <div className="w-100 row mx-0 bg-secondary text-white rounded">
      <span className="col-1 text-center text-truncate py-2 border-end">Estado</span>
      <span className="col-2 text-center text-truncate py-2 border-end">Trx Id</span>
      <span className="col-2 text-center text-truncate py-2 border-end">Fecha y hora</span>
      <span className="col-2 text-center text-truncate py-2 border-end">Origen</span>
      <span className="col-2 text-center text-truncate py-2 border-end">Cantidad</span>
      <span className="col-2 text-center text-truncate py-2 border-end">Operación</span>
      <span className="col-1 text-center text-truncate py-2">Detalles</span>
    </div>
  )
};

const DataRow = () => {
  //prettier-ignore
  return (
    <div className="w-100 row mx-0 border-bottom border-start border-end">
      <div className="col-1 d-flex justify-content-center align-items-center py-2 border-end">
        <i className="fas fa-check text-success"></i>
      </div>

      <span className="col-2 text-center text-truncate py-2 border-end">16576842</span>
      <span className="col-2 text-center text-truncate py-2 border-end">28/07/2021 10:40:52</span>
      <span className="col-2 text-center text-truncate py-2 border-end">BWise</span>
      <span className="col-2 text-center text-truncate py-2 border-end">200.000</span>
      <span className="col-2 text-center text-truncate py-2 border-end">Créditos</span>
      
      <div className="col-1 d-flex justify-content-center align-items-center py-2">
        <button type="button" className="btn btn-outline-secondary btn-sm">
        <i className="fas fa-list"></i>
        </button>
      </div>
    </div>
  )
};
