import React, { useState } from "react";

import ContentContainer from "components/layout/ContentContainer";
import OperationDebitsList from "components/Operation/Debit/OperationDebitsList";
import DebitForm from "components/Operation/Debit/DebitForm";

export default function Debit() {
  const [debit, setDebit] = useState(null);

  const closeFunction = () => {
    setDebit(null);
  };

  return (
    <ContentContainer>
      <h2 className="py-5">Débito</h2>

      <OperationDebitsList setDebit={setDebit} />

      {debit && <DebitForm closeFunction={closeFunction} />}
    </ContentContainer>
  );
}
