import React from "react";

export default function ListHeaders() {
  return (
    <div className="row w-100 mx-0 bg-secondary text-white rounded mb-2">
      <span className="col-1 text-center border-end py-2">ID</span>
      <span className="col-2 text-center border-end py-2">Código</span>
      <span className="col-2 text-center border-end py-2">Nombre</span>
      <span className="col-2 text-center border-end py-2">País</span>
      <span className="col-2 text-center border-end py-2">Provincia</span>
      <span className="col-2 text-center border-end py-2">Ciudad</span>
      <span className="col-1 text-center py-2">Estado</span>
    </div>
  );
}
