import React from "react";
import { DefaultInput, DefaultSelect } from "templates/input";

export default function SellerForm() {
  return (
    <form
      className="w-100 d-flex flex-column border rounded p-3"
      autoComplete="off"
    >
      <div className="row mx-0 mb-5">
        <h5 className="text-start mb-3">Información</h5>

        <DefaultInput label="Cédula" customClass="col-12" />
        <DefaultInput label="Nombre" customClass="col-12 col-md-6" />
        <DefaultInput label="Apellido" customClass="col-12 col-md-6" />

        <DefaultSelect
          label="Estado"
          customClass="col-12 col-md-6"
          placeholder="Selecciona una opción"
          options={[
            { value: "A", name: "Opción 1" },
            { value: "B", name: "Opción 2" },
          ]}
        />
        <DefaultSelect
          label="Región"
          customClass="col-12 col-md-6"
          placeholder="Selecciona una opción"
          options={[
            { value: "A", name: "Opción 1" },
            { value: "B", name: "Opción 2" },
          ]}
        />
      </div>

      <button type="button" className="btn btn-primary">
        Guardar
      </button>
    </form>
  );
}
