import React from "react";
import { DefaultInput, DefaultSelect, DefaultTextarea } from "templates/input";

export default function AccreditationForm() {
  return (
    <form
      className="w-100 d-flex flex-column border rounded p-3"
      autoComplete="off"
    >
      <div className="row mx-0">
        <h5 className="text-start mb-3">Formulario</h5>

        <DefaultSelect
          label="Banco"
          customClass="col-12"
          placeholder="Selecciona una opción"
          options={[
            { value: "A", name: "Opción 1" },
            { value: "B", name: "Opción 2" },
          ]}
        />

        <DefaultInput label="Nro. Comprobante" customClass="col-12 col-md-6" />
        <DefaultInput label="Valor total" customClass="col-12 col-md-6" />

        <DefaultTextarea label="Comentario" customClass="col-12" />
      </div>

      <button type="button" className="btn btn-primary">
        Guardar
      </button>
    </form>
  );
}
