import React, { useEffect, useRef } from "react";

export default function TabsButtons({ handleTab, hierarchy }) {
  const tabOneRef = useRef();

  useEffect(() => {
    tabOneRef.current.click();
  }, []);

  //prettier-ignore
  return (
    <div className="btn-group mb-5" role="group" aria-label="Consultar mi comercio">
      <input onClick={handleTab} type="radio" className="btn-check" name="btnradio" id="tab-1" autocomplete="off" value='1' ref={tabOneRef} />
      <label className="btn btn-outline-secondary" for="tab-1">Datos</label>

      <input onClick={handleTab} type="radio" className="btn-check" name="btnradio" id="tab-2" autocomplete="off" value='2' />
      <label className="btn btn-outline-secondary" for="tab-2">Acreditaciones realizadas</label>

      <input onClick={handleTab} type="radio" className="btn-check" name="btnradio" id="tab-3" autocomplete="off" value='3' />
      <label className="btn btn-outline-secondary" for="tab-3">Acreditaciones recibidas</label>

      <input onClick={handleTab} type="radio" className="btn-check" name="btnradio" id="tab-4" autocomplete="off" value='4' />
      <label className="btn btn-outline-secondary" for="tab-4">Ventas</label>

      {hierarchy && 
        <>
          <input onClick={handleTab} type="radio" className="btn-check" name="btnradio" id="tab-5" autocomplete="off" value='5' />
          <label className="btn btn-outline-secondary" for="tab-5">Jerarquía</label>
        </>
      }
    </div>
  )
}
