import React from "react";

import { DefaultInput, DefaultCheckbox,DefaultSelect } from "templates/input";

export default function UpdateForm() {
  return (
    <form className="w-100 row mx-0 border p-3" autoComplete="off">
      {/* row 1 */}
      <DefaultInput label="Primer nombre" customClass="col-3 mb-0" />
      <DefaultInput label="Segundo nombre" customClass="col-3 mb-0" />
      <DefaultInput label="Primer apellido" customClass="col-3 mb-0" />
      <DefaultInput label="Segundo apellido" customClass="col-3 mb-0" />

      {/* row 2 */}
      <DefaultInput label="Mail" type="email" customClass="col-3 mb-0" />
      <DefaultInput label="Teléfono" customClass="col-3 mb-0" />
      <DefaultInput label="Dirección" customClass="col-6 mb-0" />

      {/* row 2 */}
      <DefaultInput label="Usuario" type="email" customClass="col-6 mb-0" />
      <DefaultCheckbox
        label="Notificación por SMS"
        customClass="col-3 d-flex align-items-center justify-content-center gap-2 fw-bold"
      />
      <DefaultCheckbox
        label="Notificación por correo"
        customClass="col-3 d-flex align-items-center justify-content-center gap-2 fw-bold"
      />

        <DefaultSelect
            label="Impresión"
            customClass="col-6"
            placeholder="Selecciona una opción"
            options={[
                { value: "1", name: "A4" },
                { value: "2", name: "POS2P" },
                { value: "3", name: "POS74mm" },
            ]}
        />

      <button type="button" className="btn btn-primary w-auto ms-auto mt-3">
        Actualizar usuario
      </button>
    </form>
  );
}
