import React from "react";

import TabsButtons from "./TabsButtons";
import Data from "./Data";
import AccreditationsMade from "./AccreditationsMade";
import AccreditationsReceived from "./AccreditationsReceived";
import Sales from "./Sales";

export default function TabsContainer({
  title,
  closeBtn,
  closeFunction,
  tab,
  handleTab,
  hierarchy,
}) {
  return (
    <div className="w-100">
      <div className="d-flex justify-content-between align-items-center">
        {title && <h2 className="text-center mb-4">Información de comercio</h2>}
        {closeBtn && (
          <button
            onClick={closeFunction}
            type="button"
            className="btn btn-danger"
          >
            <i className="fas fa-times me-2"></i>
            Cerrar
          </button>
        )}
      </div>

      <TabsButtons handleTab={handleTab} hierarchy={hierarchy} />

      {tab === 1 && <Data />}
      {tab === 2 && <AccreditationsMade />}
      {tab === 3 && <AccreditationsReceived />}
      {tab === 4 && <Sales />}
    </div>
  );
}
