import React from "react";
import { DefaultInput, DefaultTextarea } from "templates/input";

export default function DebitForm({ closeFunction }) {
  return (
    <form className="w-100 d-flex flex-column" autoComplete="off">
      <div className="d-flex justify-content-between align-items-center mb-4">
        <h2 className="text-center mb-0">Detalles de débito</h2>
        <button
          onClick={closeFunction}
          type="button"
          className="btn btn-danger"
        >
          <i className="fas fa-times me-2"></i>
          Cerrar
        </button>
      </div>

      <div className="p-3 border row mx-0 rounded mb-5">
        <p className="w-100">
          <span className="me-2">Origen:</span>
          CONEXION TECNOLOGIA CONEXTONTEC SOCIEDAD ANONIMA (234824)
        </p>

        <p className="w-100">
          <span className="me-2">Destino:</span>1 AMANDA (235607)
        </p>

        <DefaultInput label="Valor" customClass="col-12" />
        <DefaultTextarea label="Comentarios" customClass="col-12" />
      </div>

      <button type="button" className="btn btn-primary">
        Guardar
      </button>
    </form>
  );
}
