import React from "react";
import { DefaultCheckbox, DefaultInput, DefaultSelect } from "templates/input";

export default function AdminMyBusinessForm() {
  return (
    <form className="w-100 d-flex flex-column" autoComplete="off">
      <div className="p-3 border row mx-0 rounded mb-5">
        <h5 className="text-start mb-3">Información general</h5>

        <DefaultInput customClass="col-12 col-md-6" label="Padre" />
        <DefaultInput customClass="col-12 col-md-6" label="Tipo" />
        <DefaultInput customClass="col-12 col-md-6" label="Código" />
        <DefaultInput customClass="col-12 col-md-6" label="Nombre" />
        <DefaultSelect
          customClass="col-12 col-md-6"
          label="País"
          placeholder="Seleccione una opción"
          options={[{ value: "1", name: "Ecuador" }]}
        />
        <DefaultSelect
          customClass="col-12 col-md-6"
          label="Provincia"
          placeholder="Seleccione una opción"
          options={[{ value: "1", name: "Pichincha" }]}
        />
        <DefaultSelect
          customClass="col-12 col-md-6"
          label="Ciudad"
          placeholder="Seleccione una opción"
          options={[{ value: "1", name: "Quito" }]}
        />
        <DefaultSelect
          customClass="col-12 col-md-6"
          label="Parroquia"
          placeholder="Seleccione una opción"
          options={[{ value: "1", name: "Quito" }]}
        />
        <DefaultSelect
          customClass="col-12"
          label="Tipo de comercio"
          placeholder="Seleccione una opción"
          options={[
            { value: "1", name: "Empresa/Distribuidor" },
            { value: "2", name: "Cadenas" },
          ]}
        />
      </div>

      <div className="p-3 border row mx-0 rounded mb-5">
        <h5 className="text-start mb-3">Información de facturación</h5>

        <DefaultSelect
          customClass="col-12"
          label="País"
          placeholder="Seleccione una opción"
          options={[
            { value: "1", name: "Ruc" },
            { value: "2", name: "Cédula" },
          ]}
        />
        <DefaultInput customClass="col-12" label="Identificación" />
        <DefaultInput customClass="col-12" label="Dirección" />
      </div>

      <div className="p-3 border row mx-0 rounded mb-5">
        <h5 className="text-start mb-3">Información del contacto</h5>

        <DefaultInput customClass="col-12 col-md-6" label="Nombre" />
        <DefaultInput customClass="col-12 col-md-6" label="Apellido" />
        <DefaultInput customClass="col-12 col-md-6" label="Correo" />
        <DefaultInput customClass="col-12 col-md-6" label="Teléfono" />
        <DefaultInput customClass="col-12 col-md-6" label="Dirección" />
        <DefaultCheckbox
          label="Notificación por SMS"
          customClass="col-12 col-md-3 d-flex align-items-center justify-content-center gap-2 fw-bold"
        />
        <DefaultCheckbox
          label="Notificación por correo"
          customClass="col-12 col-md-3 d-flex align-items-center justify-content-center gap-2 fw-bold"
        />
      </div>

      <div className="p-3 border row mx-0 rounded mb-3">
        <h5 className="text-start mb-3">Seccion banco central</h5>

        <DefaultInput customClass="col-12" label="Pin en Banco Central" />
      </div>

      <button type="button" className="btn btn-primary">
        Guardar
      </button>
    </form>
  );
}
