import React from "react";

import ListSearch from "components/AdminBusiness/AdminBusiness/ListSearch";
import ListHeaders from "components/AdminBusiness/AdminBusiness/ListHeaders";
import BusinessListItem from "components/AdminBusiness/AdminBusiness/BusinessListItem";

export default function AdminBusinessList({ setBusiness }) {
  return (
    <div className="w-100 border p-3 d-flex flex-column mb-4">
      <ListSearch />

      <ListHeaders />

      <BusinessListItem setBusiness={setBusiness} />
    </div>
  );
}
