import React from "react";

import { DefaultInput } from "templates/input";

export default function PasswordForm() {
  return (
    <form className="w-100 row mx-0 border p-3" autoComplete="off">
      {/* row 1 */}
      <DefaultInput label="Nombre" customClass="col-6 mb-0" />
      <DefaultInput label="Usuario" customClass="col-6 mb-0" />
      <DefaultInput label="Contraseña actual" customClass="col-4 mb-0" />
      <DefaultInput label="Contraseña nueva" customClass="col-4 mb-0" />
      <DefaultInput label="Repetir nueva contraseña" customClass="col-4 mb-0" />

      <button type="button" className="btn btn-primary w-auto ms-auto mt-3">
        Actualizar contraseña
      </button>
    </form>
  );
}
