import React from "react";

import ContentContainer from "components/layout/ContentContainer";
import UsersList from "components/Business/UsersList";
import UserDataForm from "components/Business/UserDataForm";

export default function AdminUsers() {
  return (
    <ContentContainer>
      <h2 className="py-5">Administrar usuarios</h2>

      <div className="w-100 d-flex align-items-start gap-3">
        <UsersList title="Usuarios" />
        <UserDataForm />
      </div>
    </ContentContainer>
  );
}
