import React from "react";
import { useHistory } from "react-router-dom";

export default function BusinessListItem({ setBusiness }) {
  const history = useHistory();

  return (
    <div className="row w-100 mx-0 border mb-2">
      <span className="col-1 text-center border-end py-2">1</span>
      <span className="col-3 text-center border-end py-2">235607</span>
      <span className="col-3 text-center border-end py-2">0401641758</span>
      <span className="col-3 text-center border-end py-2">1 AMANDA</span>

      <div className="col-1 d-flex align-items-center justify-content-center py-2 border-end">
        <button
          onClick={() => setBusiness(true)}
          type="button"
          className="btn btn-outline-secondary btn-sm"
        >
          <i className="fas fa-edit"></i>
        </button>
      </div>

      <div className="col-1 d-flex align-items-center justify-content-center py-2">
        <button
          onClick={() => history.push(`businessUsers/1`)}
          type="button"
          className="btn btn-outline-secondary btn-sm"
        >
          <i className="fas fa-user"></i>
        </button>
      </div>
    </div>
  );
}
