import React from "react";
import { useHistory } from "react-router-dom";

import logo from "images/logo.png";

import "styles/Login.css";

export default function Login() {
  const history = useHistory();

  return (
    <div className="login-screen d-flex align-items-center justify-content-center bg-light">
      <form className="border shadow rounded p-4 d-flex flex-column w-25">
        <div className="w-100 mb-4 d-flex justify-content-center">
          <img src={logo} alt="Flex logo" width="300" />
        </div>

        <div className="form-floating mb-3">
          <input
            type="text"
            className="form-control"
            placeholder="Ingresa tu usuario"
            id="emailLogin"
            name="email"
          />
          <label htmlFor="emailLogin">Usuario</label>
        </div>

        <div className="form-floating mb-3">
          <input
            type="password"
            className="form-control"
            id="passLogin"
            placeholder="Contraseña"
            name="password"
          />
          <label htmlFor="passLogin">Contraseña</label>
        </div>

        <button
          onClick={() => history.push("/userUpdate")}
          type="submit"
          className="btn btn-success"
        >
          Ingresar
        </button>
      </form>
    </div>
  );
}
