import React from "react";
import { DefaultCheckbox, DefaultInput, DefaultSelect } from "templates/input";

export default function UserDataForm() {
  return (
    <form
      className="w-100 d-flex flex-column border rounded p-3"
      autoComplete="off"
    >
      <div className="p-3 border row mx-0 rounded mb-5">
        <h5 className="text-start mb-3">Información General Usuario</h5>

        {/* row 1 */}
        <DefaultInput label="Comercio" customClass="col-3 mb-0" />
        <DefaultInput label="Primer nombre" customClass="col-3 mb-0" />
        <DefaultInput label="Primer apellido" customClass="col-3 mb-0" />
        <DefaultInput label="Segundo apellido" customClass="col-3 mb-0" />

        {/* row 2 */}
        <DefaultInput label="Mail" type="email" customClass="col-3 mb-0" />
        <DefaultInput label="Teléfono" customClass="col-3 mb-0" />
        <DefaultInput label="Dirección" customClass="col-6 mb-0" />

        {/* row 2 */}
        <DefaultInput label="Nombre de usuario" customClass="col-3 mb-0" />
        <DefaultSelect
          label="Canal de ventas"
          customClass="col-3 mb-0"
          placeholder="Selecciona una opción"
          options={[
            { value: "A", name: "Web" },
            { value: "B", name: "Física" },
          ]}
        />
        <DefaultSelect
          label="Tipo de usuario"
          customClass="col-6 mb-0"
          placeholder="Selecciona una opción"
          options={[
            { value: "A", name: "Administrador" },
            { value: "B", name: "Sub Distribuidor" },
            { value: "C", name: "Vendedor" },
          ]}
        />

        {/* Row 3 */}
        <DefaultCheckbox
          label="Notificación por SMS"
          customClass="col-3 offset-3 d-flex align-items-center justify-content-center gap-2 fw-bold mt-4"
        />
        <DefaultCheckbox
          label="Notificación por correo"
          customClass="col-3 d-flex align-items-center justify-content-center gap-2 fw-bold mt-4"
        />
      </div>

      <div className="p-3 border row mx-0 rounded mb-5">
        <h5 className="text-start mb-3">Acceso</h5>

        <DefaultSelect
          label="Canal de ventas"
          customClass="col-12 col-md-6"
          placeholder="Selecciona una opción"
          options={[
            { value: "A", name: "Web" },
            { value: "B", name: "Movil" },
          ]}
        />
        <DefaultInput customClass="col-12 col-md-6" label="Usuario" />

        <p className="col-12 mb-0 d-flex align-items-center word-break">
          <i className="fas fa-exclamation-circle text-secondary me-2 fa-lg"></i>
          Su Clave será generada automáticamente y enviada por el medio que se
          haya especificado notificar
        </p>
      </div>

      <div className="p-3 border row mx-0 rounded mb-3">
        <h5 className="text-start mb-3">Roles</h5>

        <div className="w-50 mx-auto rounded border p-2 d-flex flex-column">
          <div className="w-100 d-flex align-items-center gap-3 mb-3 border-bottom">
            <span className="w-50 text-center fw-bold rounded py-2">
              Nombre
            </span>
            <span className="w-50 text-center fw-bold rounded py-2">
              Estado
            </span>
          </div>

          <div className="w-100 d-flex align-items-center gap-3 mb-2 border-bottom pb-2">
            <span className="w-50 text-center">
              Administrador de mayoristas
            </span>
            <button type="button" className="btn btn-outline-secondary w-50">
              Sin asignar
            </button>
          </div>

          <div className="w-100 d-flex align-items-center gap-3 mb-2 border-bottom pb-2">
            <span className="w-50 text-center">Reporteador de mayoristas</span>
            <button type="button" className="btn btn-secondary w-50">
              Asignado
            </button>
          </div>

          <div className="w-100 d-flex align-items-center gap-3 mb-2 border-bottom pb-2">
            <span className="w-50 text-center">Recaudador</span>
            <button type="button" className="btn btn-secondary w-50">
              Asignado
            </button>
          </div>
        </div>
      </div>

      <button type="button" className="btn btn-primary">
        Guardar
      </button>
    </form>
  );
}
